import Phaser from 'phaser';
import { GA } from '../tools/util';
import config from '../config';

const gameConfig = config


export default class Button extends Phaser.GameObjects.Sprite {
    constructor({ scene, x = 0, y = 0, texture, data = {}, callback = () => {}, goLogo, goPrecautions, goDetail }) {
        super(scene, x, y, texture);
        scene.add.existing(this);
        this.setOrigin(0, 0);
        this.setInteractive({ pixelPerfect: true, useHandCursor: true });
        this.setData(data);
        if (goLogo) {
            this.on('pointerup', this.goLogoUrl);
        } else if (goDetail) {
            this.on('pointerup', this.goDetailUrl);
        } else {
            this.on('pointerup', (e) => { callback(scene, this, e) });
        }
    }

    goLogoUrl() {
        GA(this.scene.scene.key, 'click', 'logo');
        location.href = gameConfig.base.logo.url;
    }

    goDetailUrl() {
        GA(this.scene.scene.key, 'click', '活動詳情');
        //location.href = 'http://www.softmobile.com.tw/';
        //window.open(' ./拉霸說明.pdf');
        window.open(gameConfig.base.details.url);
    }
}