import phaser from 'phaser';
import Axios from 'axios';
import UIPlugin from './rex/ui-plugin.min';
import ContainerLitePlugin from './rex/containerliteplugin.min';
import TagTextPlugin from './rex/tagtextplugin.min';
import { GA } from './tools/util';
import { SceneName, Code } from './tools/staticData';
import { verifyIsIos } from './tools/util';
import { Preloader } from './scenes/preloader';
import { Game } from './scenes/game';
import { Award } from './scenes/award';
import { Record } from './scenes/record';
import { Message } from './scenes/message';
import config1 from './config'

const gameConfig = config1
const path = gameConfig.base.image_path;
document.title = gameConfig.base.title;
document.body.style.backgroundImage = 'url(' + path + gameConfig.base.backgroundImg + ')';
document.body.style.backgroundSize = '100%';
document.body.style.backgroundRepeat = 'no-repeat';
document.body.style.overflow = 'hidden';
document.body.style.margin = 0;
document.body.style.padding = 0;
document.body.style.position = 'relative';


const config = {
    width: 640,
    height: 1000,
    transparent: true,
    pixelArt: true,
    parent: 'content',
    scale: {
        //自動縮放到可以顯示整個遊戲畫面
        mode: Phaser.Scale.FIT,
        autoCenter: Phaser.Scale.CENTER_HORIZONTALLY
    },
    loader: {
        //同時最多載入資源數(default 4)
        maxParallelDownloads: 10,
    },
    plugins: {
        scene: [{
            key: 'rexUI',
            plugin: UIPlugin,
            mapping: 'rexUI'
        }, ],
        global: [{
            key: 'rexContainerLitePlugin',
            plugin: ContainerLitePlugin,
            start: true
        }, {
            key: 'rexTagTextPlugin',
            plugin: TagTextPlugin,
            start: true
        }, ]
    },
    scene: [
        Preloader,
        Game,
        Award,
        Record,
        Message,
    ]
};
const game = new phaser.Game(config);

Axios.onError = (data) => {
    if (data && data.message && data.message.indexOf('timeout') >= 0) {
        data = {};
        data.RtCode = Code.SUCCESS;
        data.Status = Code.TIMEOUT;
    }
    game.scene.getScenes()[0].scene.start(SceneName.Message, data);
}

window.InitialData = (code) => {
    sessionStorage.code = code;
    if (!code) {
        if (navigator && navigator.userAgent) {
            GA('InitialData', 'empty', navigator.userAgent);
        } else {
            GA('InitialData', 'empty', 'no_userAgent');
        }
    }
}

if (!verifyIsIos()) {
    document.addEventListener('copy', function(e) {
        e.clipboardData.setData('text/plain', e.srcElement.value);
        e.preventDefault(); // default behaviour is to copy any selected text
    });
}

export { game };