import { Scene } from 'phaser';
import { SceneName, Code } from '../tools/staticData';
import { GA } from '../tools/util';
import Image from '../sprites/image';
import Button from '../sprites/button';
import config from '../config';

const gameConfig = config;

export class Message extends Scene {
    constructor() {
        super({ key: SceneName.Message });

        this.data = {};
    }

    init(data) {
        this.data = data || {};
    }

    create() {
        const { RtCode, Status } = this.data;
        if (RtCode == Code.SUCCESS) {
            switch (Status) {
                case Code.PLAYED: //玩過
                    GA(this.scene.key, 'show', 'played');
                    new Image({ scene: this, texture: 'played' });
                    break;
                case Code.NOGUID: //沒guid
                    GA(this.scene.key, 'show', 'no_guid');
                    new Image({ scene: this, texture: 'toapp' });
                    break;
                case Code.TIMEOUT: //timeout
                    GA(this.scene.key, 'show', 'timeout');
                    new Image({ scene: this, texture: 'crowded' });
                    break;
                case Code.NOTSTART: //還沒開始
                    GA(this.scene.key, 'show', 'comingsoon');
                    new Image({ scene: this, texture: 'comingsoon' });
                    break;
                default: //其他錯誤
                    GA(this.scene.key, 'show', 'system_busy');
                    new Image({ scene: this, texture: 'time' });
                    break;
            }
        } else {
            // api錯誤
            GA(this.scene.key, 'show', 'system_busy');
            new Image({ scene: this, texture: 'time' });
        }

        new Button({ scene: this, texture: 'btn_Home', x: gameConfig.pram.message_btnHomeXY[0], y: gameConfig.pram.message_btnHomeXY[1], callback: this.goHome });
    }

    update() {

    }

    goHome(game) {
        GA(game.scene.key, 'click', 'home');
        game.scene.start(SceneName.Game);
    }
}