import { SceneName, Code } from '../tools/staticData';
import { getCode } from '../tools/util';

export const changePage = (scene, pageName, data) => {
  if (!getCode()) {
    // 沒guid
    const newData = {};
    newData.RtCode = Code.SUCCESS;
    newData.Status = Code.NOGUID;
    scene.scene.start(SceneName.Message, newData);
  } else if (data && data.Status === Code.PLAYED) {
    // 已玩過
    scene.scene.start(SceneName.Message, data);
  } else {
    // 正常跳頁
    return scene.scene.start(pageName, data);
  }
}

export const showToast = (scene, x = 0, y = 0, msg) => {
  scene.rexUI.add.toast({
    x,
    y,
    background: scene.rexUI.add.roundRectangle(0, 0, 2, 2, 20, '#ABABAB'),
    text: scene.add.text(0, 0, '', { fontSize: '24px' }),
    space: {
      left: 20,
      right: 20,
      top: 20,
      bottom: 20,
    },
  }).show(msg);
}