import Phaser from 'phaser';

export default class Image extends Phaser.GameObjects.Image {
  constructor({ scene, x = 0, y = 0, texture }) {
    super(scene, x, y, texture);
    scene.add.existing(this);
    this.setOrigin(0, 0);
  }

  tweens({ delay = 0, duration = 1500, x = 0, y = 0, fromAlpha = 1, endAlpha = 1 } = {}) {
    this.setAlpha(fromAlpha);
    this.scene.tweens.add({
      targets: this,
      ease: 'Power3',
      delay,
      duration,
      x,
      y,
      alpha: endAlpha 
    })
    return this;
  }
}