import { Scene } from 'phaser';
import { SceneName } from '../tools/staticData';
import { queryList } from '../tools/api';
import { changePage } from '../tools/ui';
import { copy, checkActivityTimeOver, iosVersionMoreThan10, GA } from '../tools/util';
import { showToast } from '../tools/ui';
import Image from '../sprites/image';
import Button from '../sprites/button';
import Text from '../sprites/text';
import config from '../config';
const gameConfig = config;

export class Record extends Scene {
    constructor() {
        super({ key: SceneName.Record })
        this.cellBgX = 320;

        this.dayX = 95;

        this.nameX = 278;
        this.nameWidth = 255;

        this.codeX = 505;
        this.codeWidth = 180;
        this.codeOffsetY = -20;
        this.copyOffsetY = 20;

        this.startY = 50;
        this.rowHeight = 100;

        this.dayFontStyle = {
            fontSize: '24px',
        };
        this.nameFontStyle = {
            fill: '#000'
        };
    }

    create() {
        if (checkActivityTimeOver()) {
            new Image({ scene: this, texture: 'list_bg_end' });
        } else {
            new Image({ scene: this, texture: 'list_bg' });
        }
        new Image({ scene: this, texture: 'form_head' });
        new Button({ scene: this, texture: 'btn_Home', x: gameConfig.pram.record_btnHomeXY[0], y: gameConfig.pram.record_btnHomeXY[1], callback: this.goHome });

        this.getList();
    }

    update() {

    }

    goHome(game) {
        GA(game.scene.key, 'click', 'home');
        changePage(game, SceneName.Game);
    }

    async getList() {
        try {
            // const { data } = await queryList();
            const data = await queryList();
            this.createText(data.DrawLogList);
        } catch (error) {
            console.error(error);
        }
    }

    createText(datas) {
        const {
            startY,
            rowHeight,
            cellBgX,
            dayX,
            dayFontStyle,
            nameX,
            nameWidth,
            nameFontStyle,
            codeX,
            codeWidth,
            codeOffsetY,
            copyOffsetY,
        } = this;
        let currentY = 0;
        const container = this.add.rexContainerLite(0, 0);

        // const self = this;
        datas.forEach(({ DrawDay, PrizeName, PrizeSerial, PrizeUrl }, index) => {
            currentY = startY + (index * rowHeight);

            const bg = new Image({ scene: this, x: cellBgX, y: currentY, texture: ((index + 1) % 2 == 0) ? 'form_even' : 'form_odd' });
            bg.setOrigin(0.5, 0.5);

            const newDay = DrawDay.split(" ")[0].substr(5);
            const dayText = new Text(this, dayX, currentY, newDay, dayFontStyle);
            // const nameText = new Text(this, nameX, currentY, PrizeName, nameFontStyle);
            // nameText.autoSizeFont(nameWidth, rowHeight, 26);
            var tags = {
                tag0: {
                    underline: {
                        color: '#000',
                        thinkness: 2,
                        offset: 4
                    },
                },
            };
            // const nameText = this.add.rexTagText(nameX, currentY, PrizeName);
            const nameText = this.add.rexTagText(nameX, currentY, '<class="tag0">' + PrizeName + '</class>', { tags: tags });
            nameText.setOrigin(0.5, 0.5);
            nameText.setColor('#000');
            nameText.setFontSize(20);

            nameText.setInteractive({ useHandCursor: true });
            nameText.on('pointerup', (e) => { this.onLinkClick(PrizeUrl) });
            // Text.SAutoSizeFont(nameText, nameWidth, rowHeight, 17);

            let codeText, btnCopy;
            if (iosVersionMoreThan10()) {
                codeText = new Text(this, codeX, currentY + codeOffsetY, PrizeSerial, nameFontStyle);
                btnCopy = new Button({ scene: this, x: codeX, y: currentY + copyOffsetY, texture: 'record_btnCopy', data: { PrizeSerial }, callback: this.onCopyClick });
                btnCopy.setOrigin(0.5, 0.5);
                container.add(btnCopy);
            } else {
                codeText = new Text(this, codeX, currentY, PrizeSerial, nameFontStyle);
            }
            codeText.autoSizeFont(codeWidth, rowHeight);
            // codeText.setData({ PrizeSerial });
            // codeText.setInteractive({ useHandCursor: true });
            // codeText.on('pointerup', (e) => { this.onCopyClick(this, codeText, e) });


            // const press = this.rexUI.add.press(codeText, { time: 1000 });
            // press.on('pressstart', (press) => {
            //   const { gameObject, x, y } = press;
            //   showToast(this, x, y, '已複製序號');
            // });

            // press.on('pressend', (press) => {
            //   console.log('press: ', press);
            //   const { gameObject, x, y } = press;
            //   console.log('serial: ', gameObject.data.values.PrizeSerial);
            //   copy(gameObject.data.values.PrizeSerial);
            //   // showToast(this, x, y, '已複製序號');
            // });

            container.add(bg);
            container.add(dayText);
            container.add(nameText);
            container.add(codeText);

        });
        container.height = datas.length * rowHeight;

        const scrollView = this.rexUI.add.scrollablePanel({
            x: 0,
            y: 200,
            width: 630,
            height: 700,
            panel: {
                child: container,
            },
            // slider: {
            //   track: this.rexUI.add.roundRectangle(0, 0, 0, 0, 7, 0xFFFFFF, 0.2),
            //   thumb: this.rexUI.add.roundRectangle(0, 0, 0, 0, 10, 0xFFFFFF, 0.3),
            // },
        });
        scrollView.setOrigin(0, 0);
        scrollView.layout();
    }

    onLinkClick(url) {
        GA(this.scene.key, 'click', 'url');
        location.href = url;
    }

    onCopyClick(scene, btn, e) {
        scene.input.enabled = false;
        const data = btn.data.values;
        copy(data.PrizeSerial);
        showToast(scene, e.upX, e.upY, '已複製序號');
        GA(scene.scene.key, 'click', 'copy');

        setTimeout(function() {
            scene.input.enabled = true;
        }, 1500);
    }
}