import { Scene } from 'phaser';
import { SceneName } from '../tools/staticData';
import { copy, iosVersionMoreThan10, GA } from '../tools/util';
import { showToast, changePage } from '../tools/ui';
import Text from '../sprites/text';
import Image from '../sprites/image';
import Button from '../sprites/button'
import config from '../config';
const gameConfig = config;
export class Award extends Scene {
    constructor() {
        super({ key: SceneName.Award })

        this.data = {};
        this.toast = null;
    }

    init(data) {
        this.data = data;
    }

    create() {
        if (this.data.IsPrize == 0) {
            new Image({ scene: this, texture: 'no_award_bg' });
            new Button({ scene: this, texture: 'btn_Record', x: gameConfig.pram.award_btnRecordXY[0], y: gameConfig.pram.award_btnRecordXY[1], callback: this.goRecord });
            new Button({ scene: this, texture: 'btn_Detail', x: gameConfig.pram.award_btnDetailXY[0], y: gameConfig.pram.award_btnDetailXY[1], goDetail: true });
        } else {
            new Image({ scene: this, texture: 'award_bg' });
            new Button({ scene: this, texture: 'btn_Record', x: gameConfig.pram.award_btnRecordXY[0], y: gameConfig.pram.award_btnRecordXY[1], callback: this.goRecord });
            new Button({ scene: this, texture: 'btn_Detail', x: gameConfig.pram.award_btnDetailXY[0], y: gameConfig.pram.award_btnDetailXY[1], goDetail: true });
            new Button({ scene: this, texture: 'btn_Use', x: gameConfig.pram.award_btnUseXY[0], y: gameConfig.pram.award_btnUseXY[1], callback: () => { this.goAward(this.data.PrizeUrl) } });

            //this.load.image('award_image', this.data.PrizePicUrl);
            this.load.once('complete', () => { this.onImageLoadComplete() });
            this.load.start();
            GA(this.scene.key, 'show', this.data.PrizeName);
        }

    }

    update() {

    }

    onImageLoadComplete() {

        new Image({ scene: this, texture: '03_Awardprize_titlebg2' });
        const txtPriceName = new Text(this, 320, 205, this.data.PrizeName);
        txtPriceName.setColor('#fff');
        txtPriceName.autoSizeFont(500, 80);

        const txtCode = new Text(this, 320, 480, this.data.PrizeSerial);
        txtCode.setColor('#000');
        txtCode.autoSizeFont(410, 45);

        const container = this.add.container(320, 480);
        container.setSize(440, 60);
        container.setInteractive({ useHandCursor: true });
        // const press = this.rexGestures.add.press(container, {
        //   enable: true,
        //   time: 1000,
        //   // threshold: 9,
        // });

        // press.on('pressstart', (press) => {
        //   console.log('pressstart');
        //     copy(this.data.PrizeSerial);
        //     showToast(this, press.x, press.y, '已複製序號');
        // });
        // press.on('pressend', function(press){
        //   console.log('pressend');
        // });
        if (iosVersionMoreThan10()) {
            container.on('pointerup', (e) => {
                copy(this.data.PrizeSerial);
                showToast(this, e.upX, e.upY, '已複製序號');
                GA(this.scene.key, 'click', 'copy');
            });
        }
    }

    goAward(url) {
        GA(this.scene.key, 'click', 'url');
        location.href = url;
    }

    goRecord(scene) {
        GA(scene.scene.key, 'click', SceneName.Record);
        changePage(scene, SceneName.Record);
    }
}