import { Scene } from 'phaser';
import { SceneName, Code } from '../tools/staticData';
import { queryIsDraw, queryDrawResult } from '../tools/api';
import { changePage } from '../tools/ui';
import { getCode, checkActivityStarted, checkActivityTimeOver, GA, getRndInteger } from '../tools/util';
import Image from '../sprites/image';
import Button from '../sprites/button';
import config from '../config';
//import Sprite from '../sprites/sprite';
const gameConfig = config.pram //設定值
let gameStatus = 0; //決定遊戲狀態
let startRun = false; //轉盤是否開始轉
let startBtn; //開始按鈕
let winPosition = JSON.parse(JSON.stringify(gameConfig.winPosition)); //中獎的位置
let losePosition = JSON.parse(JSON.stringify(gameConfig.losePosition)); //沒中獎的位置
let endPosition = [0, 0, 0]; //拉霸結束的位置
let slot_init_offset = JSON.parse(JSON.stringify(gameConfig.slot_init_offset)); //拉霸初始位置
let speed = JSON.parse(JSON.stringify(gameConfig.speeed)); //遊戲初始速度
let speedLose = JSON.parse(JSON.stringify(gameConfig.speedLose)); //速度高於時減速
let speedRange = JSON.parse(JSON.stringify(gameConfig.speedRange)); //一次減速的速度
let goData = null; //跳頁資訊
let goFlag = 0; //跳頁flag

export class Game extends Scene {
    constructor() {
        super({ key: SceneName.Game });
    }

    create() {
        this.apiCheck();
        this.arrRunIcon = []; //存放轉盤的陣列
        if (checkActivityTimeOver()) {
            new Image({ scene: this, texture: 'main_bg' });
        } else {
            new Image({ scene: this, texture: 'main_bg' });
            this.sun = this.add.sprite(gameConfig.slot_bgXY[0] + gameConfig.sunXY_offset[0], gameConfig.slot_bgXY[1] + gameConfig.sunXY_offset[1], 'rotate_sun');
            this.sun_Scale = 1;
            new Image({ scene: this, x: gameConfig.slot_bgXY[0], y: gameConfig.slot_bgXY[1], texture: 'slot_bg' });
            this.slot1 = this.add.tileSprite(slot_init_offset[0][0] + gameConfig.slot_bgXY[0], slot_init_offset[0][1] + gameConfig.slot_bgXY[1], slot_init_offset[0][2], slot_init_offset[0][3], 'run_list_1');
            this.slot2 = this.add.tileSprite(slot_init_offset[1][0] + gameConfig.slot_bgXY[0], slot_init_offset[1][1] + gameConfig.slot_bgXY[1], slot_init_offset[1][2], slot_init_offset[1][3], 'run_list_2');
            this.slot3 = this.add.tileSprite(slot_init_offset[2][0] + gameConfig.slot_bgXY[0], slot_init_offset[2][1] + gameConfig.slot_bgXY[1], slot_init_offset[2][2], slot_init_offset[2][3], 'run_list_3');
            //放入每一列的拉霸
            this.arrRunIcon.push(this.slot1, this.slot2, this.slot3);

            //設定拉霸的初始位置
            this.arrRunIcon.forEach(function(item) {
                item.tilePositionX = gameConfig.slot_init_XY[0];
                item.tilePositionY = gameConfig.slot_init_XY[1];
            });
            new Image({ scene: this, x: gameConfig.slot_bgXY[0] + 17, y: gameConfig.slot_bgXY[1] - 150, texture: 'slot_shadow' });
            startBtn = new Button({ scene: this, x: gameConfig.game_btnStartXY[0], y: gameConfig.game_btnStartXY[1], texture: 'game_btnStart', callback: this.startScorll });
            new Button({ scene: this, texture: 'logo', goLogo: true });
            new Button({ scene: this, texture: 'btn_Detail', x: gameConfig.game_btnDetailXY[0], y: gameConfig.game_btnDetailXY[1], goDetail: true });
            new Button({ scene: this, texture: 'btn_Record', x: gameConfig.game_btnRecordXY[0], y: gameConfig.game_btnRecordXY[1], callback: this.goRecord });
        }
    }

    update() {
        //避免拉霸座標為負
        for (let i = 0; i < this.arrRunIcon.length; i++) {
            if (this.arrRunIcon[i].tilePositionY < 0) {
                this.arrRunIcon[i].tilePositionY = this.arrRunIcon[i].tilePositionY + 466;
            }
        }
        if (startRun == true) this.run();
        // gameStatus: 0->初始狀態 1->拉霸減速 2->太陽動畫 3->跳去得獎頁面
        switch (gameStatus) {
            case 1:
                this.slowScorll(0);
                if (speed[0] == 0) this.slowScorll(1);
                if (speed[1] == 0) this.slowScorll(2);
                break;
            case 2:
                this.sun.angle += 5;
                if (this.sun_Scale < 1.3) {
                    this.sun_Scale += 0.003;
                    this.sun.scale = (this.sun_Scale, this.sun_Scale);
                } else {
                    gameStatus = 3;
                    goFlag = 1;
                }
                break;
            case 3:
                this.goAward();
                break;
            default:
                return 0;
        }
    }
    async apiCheck() { //api確認
        const data = await queryIsDraw();
        if (data.RtCode !== '0000' || data.Status !== '0')
            this.scene.start(SceneName.Message, data);
    }

    async startScorll(scene) { //按下開始按鈕
            scene.input.enabled = false;
            if (!checkActivityStarted()) {
                const data = {};
                data.RtCode = Code.SUCCESS;
                data.Status = Code.NOTSTART;
                scene.scene.start(SceneName.Message, data);
            }
            if (!getCode()) {
                // 沒guid
                const newData = {};
                newData.RtCode = Code.SUCCESS;
                newData.Status = Code.NOGUID;
                scene.scene.start(SceneName.Message, newData);
            } else {
                GA(scene.scene.key, 'click', 'start');
                const data = await queryDrawResult();
                if (data.RtCode === '0000' && data.Status === '0') {
                    startRun = true;
                    goData = data;
                    if (data.IsPrize === 1) {
                        let randomNum = getRndInteger(0, winPosition.length - 1)
                        endPosition = winPosition[randomNum];
                    } else {
                        let randomNum = getRndInteger(0, losePosition.length - 1);
                        endPosition = losePosition[randomNum];
                    }
                    //randomNum = -1;
                    setTimeout(function() {
                        gameStatus = 1;
                    }, 1000);
                } else {
                    scene.scene.start(SceneName.Message, data);
                }
            }
        }
        //拉霸動畫
    run() {
        for (let i = 0; i < this.arrRunIcon.length; i++) {
            this.arrRunIcon[i].tilePositionY = this.arrRunIcon[i].tilePositionY - speed[i];
        }
    }

    //拉霸減速
    slowScorll(num) {
        if (speed[num] > speedLose[num]) {
            speed[num] = speed[num] - speedRange[num];
        } else if (Math.abs(endPosition[num] - this.arrRunIcon[num].tilePositionY == 0)) {
            speed[num] = 0;
            this.arrRunIcon[num].tilePositionY = endPosition[num];
            if (num == gameConfig.size - 1) gameStatus = 2;
        }
    }

    async goAward() {
        if (gameStatus == 3 && goFlag == 1) {
            gameStatus = 0;
            startRun = false;
            speed = JSON.parse(JSON.stringify(gameConfig.speeed));;
            goFlag = 0;
            this.input.enabled = true;
            changePage(this, SceneName.Award, goData);
            goData = null;
        }
    }

    goRecord(scene) {
        if (!checkActivityStarted()) {
            const data = {};
            data.RtCode = Code.SUCCESS;
            data.Status = Code.NOTSTART;
            scene.scene.start(SceneName.Message, data);
        } else {
            GA(scene.scene.key, 'click', SceneName.Record);
            changePage(scene, SceneName.Record);
        }
    }
}

