import Phaser from 'phaser';

export default class AwardText extends Phaser.GameObjects.Text {
  constructor(scene, x = 0, y = 0, text, style ) {
    super(scene, x, y, text, style);
    scene.add.existing(this);
    this.setOrigin(0.5, 0.5);
  }

  autoSizeFont(w, h, maxFontSize) {
    AwardText.SAutoSizeFont(this, w, h, maxFontSize);
    // this.setFontSize(maxFontSize);
    // if (typeof this.defaultFontSize === "undefined") {
    //   this.defaultFontSize = parseInt(this.style.fontSize.replace("px", ""));
    // }

    // var width = w || this.getBounds().width;
    // var height = h || this.getBounds().height;

    // var size = this.defaultFontSize;
    // var family = this.style.fontFamily;

    // if (width > 0 && height > 0) {
    //   var metrics = this.context.measureText(this.text);
    //   while ((metrics.width > width) && size > 4) {
    //     --size;
    //     this.context.font = size.toString() + "px " + family;
    //     metrics = this.context.measureText(this.text);
    //   }
    //   this.setFontSize(size);
    // }
  }

  static SAutoSizeFont(text, w, h, maxFontSize = 50) {
    text.setFontSize(maxFontSize);
    if (typeof text.defaultFontSize === "undefined") {
      text.defaultFontSize = parseInt(text.style.fontSize.replace("px", ""));
    }

    var width = w || text.getBounds().width;
    var height = h || text.getBounds().height;

    var size = text.defaultFontSize;
    var family = text.style.fontFamily;

    if (width > 0 && height > 0) {
      var metrics = text.context.measureText(text.text);
      while ((metrics.width > width) && size > 4) {
        --size;
        text.context.font = size.toString() + "px " + family;
        metrics = text.context.measureText(text.text);
      }
      text.setFontSize(size);
    }
  }
}